.navbar {
    position: absolute;
    background-color: #060b26;
    height: 100vh;
    width: 60px;
    display: flex;
    justify-content: start;
    align-items: center;
    z-index: 11;
  }
  .map-menu{
    margin-top: 55vh;
    margin-left: 10px;
  }
  .map-menu-item{
    margin-right: 8px;
    display: flex;
    justify-content: start;
    align-items: center;
